import segmentTexture from './textures/segment.png';
import {
    BoxGeometry,
    Color,
    DoubleSide,
    Group,
    Mesh,
    MeshLambertMaterial,
    MeshStandardMaterial,
    TextureLoader,
} from 'three';
import { TextGeometry } from 'three/examples/jsm/geometries/TextGeometry';
import { FontLoader } from 'three/examples/jsm/loaders/FontLoader';
import { SEGMENT_WIDTH, SEGMENT_HEIGHT, SEGMENT_DEPTH } from './const';
import { Host } from './Host';

const textureLoader = new TextureLoader();
const fontLoader: FontLoader = new FontLoader();

export class Segment {
    #id: string;
    #hosts: Array<Host> = [];
    // @ts-ignore
    #textInstance: Mesh;
    #instance: Group;

    constructor(id: string, x: number, z: number, text: string) {
        this.#id = id;
        this.#instance = this.create(id, x, z);
        this.createText(text);
    }

    create(id: string, x: number, z: number): Group {
        const texture = textureLoader.load(segmentTexture)
        const segmentMaterial: MeshStandardMaterial = new MeshStandardMaterial({
            name: `segment_${id}`,
            map: texture
        });
        const segment: BoxGeometry = new BoxGeometry(
            SEGMENT_WIDTH,
            SEGMENT_HEIGHT,
            SEGMENT_DEPTH
        );
        const segmentMesh: Mesh = new Mesh(segment, segmentMaterial);
        segmentMesh.rotation.x = -1.57;
        segmentMesh.castShadow = true;
        const segmentGroup: Group = new Group();

        segmentGroup.add(segmentMesh);

        segmentGroup.position.set(x, SEGMENT_DEPTH / 2, z);
        return segmentGroup;
    }

    createText(text: string) {
        fontLoader.load(
            'https://cdn.rawgit.com/mrdoob/three.js/master/examples/fonts/helvetiker_regular.typeface.json',
            (font) => {
                const geometry: TextGeometry = new TextGeometry(text, {
                    font: font,
                    size: 40,
                    height: 4,
                    curveSegments: 1,
                    bevelEnabled: true,
                    bevelThickness: 1,
                    bevelSize: 1,
                    bevelSegments: 5,
                });

                let material: MeshLambertMaterial = new MeshLambertMaterial({
                    color: 0xf3ffe2,
                    name: 'text',
                    side: DoubleSide,
                });

                const textMesh = new Mesh(geometry, material);
                textMesh.position.set(-1.8, 0, 1.8);
                textMesh.rotation.x = -Math.PI / 2;
                textMesh.scale.multiplyScalar(0.01);

                this.#instance.add(textMesh);
                this.#textInstance = textMesh;
            }
        );
    }

    createHost(texture: any, id: string, x: number, z: number, text: string) {
        const host = new Host(texture, this.#instance, id, x, z, text);
        this.#instance.add(host.instance);
        this.#hosts.push(host);
        return host;
    }

    get instance() {
        return this.#instance;
    }
}
