import {
    BoxGeometry,
    Group,
    Mesh,
    MeshLambertMaterial,
    TextureLoader,
    MeshStandardMaterial,
    MeshBasicMaterial,
    Color,
} from 'three';
import { TextGeometry } from 'three/examples/jsm/geometries/TextGeometry';
import { FontLoader } from 'three/examples/jsm/loaders/FontLoader';
import { HOST_WIDTH, HOST_HEIGHT, HOST_DEPTH, SEGMENT_DEPTH } from './const';
const fontLoader: FontLoader = new FontLoader();

export class Host {
    #id: string;
    #instance: Group;
    //@ts-ignore
    #textInstance: Mesh;
    #segment: any;
    #texture: any;
    #modal: Mesh;

    constructor(
        texture: any,
        segment: any,
        id: string,
        x: number,
        z: number,
        text: string
    ) {
        this.#texture = texture;
        this.#segment = segment;
        this.#id = id;
        this.#modal = this.createModal();
        this.#instance = this.create(id, x, z);
        this.createText(text);
        // this.#instance.add(this.#modal)
    }

    create(id: string, x: number, z: number): Group {
        const hostMaterial: MeshStandardMaterial = new MeshStandardMaterial({
            name: `host_${id}`,
            map: this.#texture,
        });

        const host: BoxGeometry = new BoxGeometry(
            HOST_WIDTH,
            HOST_HEIGHT,
            HOST_DEPTH
        );

        const hostMesh: Mesh = new Mesh(host, hostMaterial);
        hostMesh.castShadow = true;
        hostMesh.name = 'host';
        hostMesh.userData._modal = this.#modal
        
        const hostGroup = new Group();
        hostGroup.add(hostMesh);
        
        this.#segment.add(hostGroup);
        hostGroup.position.set(x, HOST_DEPTH + SEGMENT_DEPTH, z);

        return hostGroup;
    }

    createText(text: string) {
        fontLoader.load(
            'https://cdn.rawgit.com/mrdoob/three.js/master/examples/fonts/helvetiker_regular.typeface.json',
            (font) => {
                const geometry: TextGeometry = new TextGeometry(text, {
                    font: font,
                    size: 12,
                    height: 5,
                    curveSegments: 1,
                    bevelEnabled: true,
                    bevelThickness: 1,
                    bevelSize: 1,
                    bevelSegments: 1,
                });

                let material: MeshLambertMaterial = new MeshLambertMaterial({
                    color: 0xf3ffe2,
                    name: 'text',
                });

                const textMesh = new Mesh(geometry, material);
                textMesh.name = 'hostText';
                textMesh.position.set(0.2, 0.4, 0);
                textMesh.rotation.x = -Math.PI / 2;
                textMesh.rotation.y = Math.PI / 2;
                textMesh.scale.multiplyScalar(0.01);
                this.#textInstance = textMesh;
                this.#instance.add(textMesh);
            }
        );
    }

    createModal(){
        const modalGeometry = new BoxGeometry(4, 8, 0.1);
        const modalMaterial = new MeshBasicMaterial({
            color: new Color(0,255,0),
        });
        const modalMesh = new Mesh(modalGeometry, modalMaterial);
        modalMesh.rotation.y = Math.PI / 2;

        const modalGroup = new Group()
        modalGroup.add(modalMesh)

        fontLoader.load(
            'https://cdn.rawgit.com/mrdoob/three.js/master/examples/fonts/helvetiker_regular.typeface.json',
            (font) => {
                const geometry: TextGeometry = new TextGeometry('test', {
                    font: font,
                    size: 12,
                    height: 5,
                    curveSegments: 1,
                    bevelEnabled: true,
                    bevelThickness: 1,
                    bevelSize: 1,
                    bevelSegments: 1,
                });

                let material: MeshLambertMaterial = new MeshLambertMaterial({
                    // color: 0xf3ffe2,
                    color: 0x000000,
                    name: 'text',
                });

                const textMesh = new Mesh(geometry, material);
                textMesh.position.set(0,0,0);
                textMesh.rotation.x = -Math.PI / 2;
                textMesh.rotation.y = Math.PI / 2;
                textMesh.scale.multiplyScalar(0.01);
                // modalGroup.add(textMesh)
            },
        );

        return modalGroup
    }
    

    get instance() {
        return this.#instance;
    }
}
