import hostImage from './textures/host.jpg';
import { TextureLoader } from 'three';
import { InteractiveMap } from './InteractiveMap';
import { Position, Size } from './types';
const textureLoader = new TextureLoader();

const rendererSizes: Size = {
    width: window.innerWidth,
    height: window.innerHeight,
};
const lightPositions: Position = { x: 10, y: 10, z: -4 };
const IM: InteractiveMap = new InteractiveMap(rendererSizes, lightPositions);

const internetSegment = IM.createSegment('Internet', -9, -3, 'Internet');
const DMZSegment = IM.createSegment('DMZ', -3, -3, 'DMZ');
const DataCenterSegment = IM.createSegment('Data Center', 3, -3, 'Data Center');
const OfficeUsersSegment = IM.createSegment(
    'Office users',
    9,
    -3,
    'Office users'
);
const ASUSegment = IM.createSegment('ASU', 15, -3, 'ASU');

textureLoader.load(hostImage, (texture) => {
    texture.repeat.x = 0.4;
    texture.repeat.y = 0.4;
    texture.offset.set(0.5, 0.5);
    internetSegment.createHost(texture, 'Kali', -1.5, -1.5, 'Kali');
    internetSegment.createHost(
        texture,
        'ProxyChecker',
        0,
        -1.5,
        'Proxy\nChecker'
    );

    DMZSegment.createHost(texture, 'CMS Drupal', -1.5, -1.5, 'CMS\n Drupal');
    DMZSegment.createHost(texture, 'Web Server 2', 0, -1.5, 'Web\n Server 2');
    DMZSegment.createHost(
        texture,
        'Appache Tomcat',
        1.5,
        -1.5,
        'Appache\n Tomcat'
    );
    DMZSegment.createHost(
        texture,
        'Security Onion',
        -1.5,
        0,
        'Security\n Onion'
    );

    DataCenterSegment.createHost(
        texture,
        'MS Active Directory',
        -1.5,
        -1.5,
        'MS Active \nDirectory'
    );
    DataCenterSegment.createHost(
        texture,
        'MS Exchange',
        0,
        -1.5,
        'MS \nExchange'
    );
    DataCenterSegment.createHost(
        texture,
        'MS FileServer',
        1.5,
        -1.5,
        'MS \nFileServer'
    );
    DataCenterSegment.createHost(
        texture,
        'Database Server',
        -1.5,
        -0.5,
        'Database \nServer'
    );
    DataCenterSegment.createHost(
        texture,
        'SSH Server',
        0,
        -0.5,
        'SSH \nServer'
    );
    DataCenterSegment.createHost(
        texture,
        'Redmine Server',
        1.5,
        -0.5,
        'Redmine \nServer'
    );
    DataCenterSegment.createHost(
        texture,
        'CMS Made Simple',
        -1.5,
        0.5,
        'CMS Made \nSimple'
    );
    DataCenterSegment.createHost(
        texture,
        'ViPNet IDS HS',
        0,
        0.5,
        'ViPNet \nIDS HS'
    );
    DataCenterSegment.createHost(
        texture,
        'ViPNet IDS NS',
        1.5,
        0.5,
        'ViPNet \nIDS NS'
    );
    DataCenterSegment.createHost(
        texture,
        'ViPNet TIAS',
        1.5,
        1.5,
        'ViPNet \nTIAS'
    );

    OfficeUsersSegment.createHost(
        texture,
        'Administrator Workstation',
        -1.5,
        -1.5,
        'Administrator\n Workstation'
    );
    OfficeUsersSegment.createHost(
        texture,
        'Manager Workstation 1',
        0,
        -1.5,
        'Manager\n Workstation 1'
    );
    OfficeUsersSegment.createHost(
        texture,
        'Response Team Workstation',
        1.5,
        -1.5,
        'Response\n Team Workstation'
    );
    OfficeUsersSegment.createHost(
        texture,
        'Insider Workstation',
        -1.5,
        0,
        'Insider Workstation'
    );
    OfficeUsersSegment.createHost(
        texture,
        'Developer Workstation',
        0,
        0,
        'Developer\n Workstation'
    );
    OfficeUsersSegment.createHost(
        texture,
        'Manager Workstation 2',
        1.5,
        0,
        'Manager\n Workstation 2'
    );

    ASUSegment.createHost(texture, 'SCADA IGSS', -1.5, -1.5, 'SCADA \nIGSS');

    const router1 = IM.createRouter('router1', -6, 2);
    const router2 = IM.createRouter('router2', 0, 2);
});
