import bg from './textures/floor.png';
import GUI from 'lil-gui';
import {
    WebGLRenderer,
    PCFSoftShadowMap,
    PointLight,
    Scene,
    MeshBasicMaterial,
    Mesh,
    TextureLoader,
    BoxGeometry,
    SphereGeometry,
    Group,
    Vector2,
    RepeatWrapping,
    Color,
    DirectionalLight,
    OrthographicCamera,
    NearestFilter,
    DirectionalLightHelper,
    MeshStandardMaterial,
    Raycaster,
    PlaneGeometry,
    Material,
    CanvasTexture,
    Texture,
    SpriteMaterial,
    Sprite,
} from 'three';
import { FontLoader } from 'three/examples/jsm/loaders/FontLoader';
import { EffectComposer } from 'three/examples/jsm/postprocessing/EffectComposer.js';
import { RenderPass } from 'three/examples/jsm/postprocessing/RenderPass.js';
import { ShaderPass } from 'three/examples/jsm/postprocessing/ShaderPass.js';
import { OutlinePass } from 'three/examples/jsm/postprocessing/OutlinePass.js';
import { FXAAShader } from 'three/examples/jsm/shaders/FXAAShader.js';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js';

import { Position, Size, HostsConnection } from './types';
import { Segment } from './Segment';
import { Router } from './Router';

const fontLoader: FontLoader = new FontLoader();

export class InteractiveMap {
    #scene: Scene;
    #camera: any;
    #renderer: WebGLRenderer;
    #light: any;
    #textureLoader: TextureLoader;
    #floorSize: Position = { x: 90, y: 0.5, z: 90 };
    #segments: Array<Segment> = [];
    #hostsConnections: Array<HostsConnection> = [];
    // @ts-ignore
    outlinePass: OutlinePass;
    #gui: any;
    selectedObjects = [];
    raycaster = new Raycaster();
    mouse = new Vector2();
    // @ts-ignore
    composer: EffectComposer;
    // @ts-ignore
    activeModal;
    constructor(rendererSize: Size, lightPositions: Position) {
        // this.#gui = new GUI();
        this.#scene = new Scene();
        this.#scene.background = new Color(0x999999);

        this.#camera = this.createCamera();

        this.#renderer = this.createWebGLRenderer(
            rendererSize.width,
            rendererSize.height
        );

        this.#light = this.createLight(
            lightPositions.x,
            lightPositions.y,
            lightPositions.z
        );

        this.#textureLoader = this.createTextureLoader();

        this.#scene.add(this.#camera);

        this.createFloor();

        this.initPostProcessing();

        let controls = new OrbitControls(
            this.#camera,
            this.#renderer.domElement
        );
        controls.maxPolarAngle = (0.9 * Math.PI) / 2.2;
        controls.maxZoom = 7;
        controls.minZoom = 1;

        this.createLine(-8.5, 2, 1, -9, 0.3, 3.5);
        this.createLine(-3.5, 2, 1, -3, 0.3, 3.5);
        this.createLine(-2.3, 2, 1, -2.8, 0.3, 3.5);
        this.createLine(2, 1, 2, 3, 0, 2.1);
        this.createLine(5.5, 2, 7, 9, 0.5, 3.1);
        this.createLine(7.8, 3.5, 14.5, 15, 1.2, 4.6);
        this.render();
    }

    createLine(
        line1Start: number,
        line1End: number,
        line1Length: number,
        line2Start: number,
        line2End: number,
        line2Length: number
    ) {
        const lineMaterial: MeshStandardMaterial = new MeshStandardMaterial({
            color: 'red',
        });
        const line1: BoxGeometry = new BoxGeometry(line1Length, 0.1, 0.1);
        const lineMesh1: Mesh = new Mesh(line1, lineMaterial);
        lineMesh1.position.set(line1Start, 0, line1End);

        const line2: BoxGeometry = new BoxGeometry(0.1, 0.1, line2Length);
        const lineMesh2: Mesh = new Mesh(line2, lineMaterial);
        lineMesh2.position.set(line2Start, 0, line2End);
        this.#scene.add(lineMesh1, lineMesh2);
    }

    createRouter(id: string, x: number, z: number) {
        const router = new Router(id, x, z);
        this.#scene.add(router.instance);
    }

    initPostProcessing() {
        this.composer = new EffectComposer(this.#renderer);

        const renderPass = new RenderPass(this.#scene, this.#camera);
        this.composer.addPass(renderPass);

        this.outlinePass = new OutlinePass(
            new Vector2(window.innerWidth, window.innerHeight),
            this.#scene,
            this.#camera
        );
        this.outlinePass.visibleEdgeColor = new Color(255, 0, 0);
        this.outlinePass.hiddenEdgeColor = new Color(255, 0, 0);
        this.outlinePass.edgeThickness = 1;
        this.outlinePass.edgeStrength = 10;
        this.composer.addPass(this.outlinePass);

        const effectFXAA = new ShaderPass(FXAAShader);
        effectFXAA.uniforms['resolution'].value.set(
            1 / window.innerWidth,
            1 / window.innerHeight
        );
        this.composer.addPass(effectFXAA);

        this.#renderer.domElement.style.touchAction = 'none';
        this.#renderer.domElement.addEventListener('pointermove', (event) =>
            this.onPointerMove(event)
        );
        this.#renderer.domElement.addEventListener('pointerdown', (event) =>
            this.onPointerDown(event)
        );
    }

    onPointerMove(event: any) {
        if (event.isPrimary === false) return;
        this.mouse.x = (event.clientX / window.innerWidth) * 2 - 1;
        this.mouse.y = -(event.clientY / window.innerHeight) * 2 + 1;

        this.checkIntersection();
    }

    onPointerDown(event: any) {
        if (event.isPrimary === false || this.selectedObjects.length === 0) return;
        const el: Mesh = this.selectedObjects[0]
        const modal: Group = el.userData._modal;
        // if (this.activeModal){
        //     this.#scene.remove(this.activeModal.children[0])
        //     this.#scene.remove(this.activeModal)
        //     el.parent.remove(this.activeModal.children[0])
        //     el.parent.remove(this.activeModal)
        // }
        this.activeModal = modal;
        // @ts-ignore
        // el.parent.add(modal)
    }

    checkIntersection() {
        this.raycaster.setFromCamera(this.mouse, this.#camera);

        const intersects = this.raycaster.intersectObject(this.#scene, true);

        if (intersects.length > 0) {
            const selectedObject = intersects[0].object;
            this.addSelectedObject(selectedObject);
            this.outlinePass.selectedObjects = this.selectedObjects;
        } else {
            // outlinePass.selectedObjects = [];
        }
    }

    addSelectedObject(object: any) {
        this.selectedObjects = [];
        if (object.name == 'hostText') {
            const host = object.parent.children.find(
                (el: any) => el.name == 'host'
            );
            //@ts-ignore
            this.selectedObjects.push(host);
        } else if (object.name == 'host') {
            //@ts-ignore
            this.selectedObjects.push(object);
        }
    }

    createSegment(id: string, x: number, z: number, text: string): Segment {
        const segment = new Segment(id, x, z, text);
        this.#segments.push(segment);
        this.#scene.add(segment.instance);
        return segment;
    }

    createHostsConnection(id: string, length: number): void {
        const hostConnectionMaterial = new MeshBasicMaterial({
            color: 0xff0000,
            name: 'hostsConnection',
        });
        // TODO: Написать автоматическое вычисление длины между двумя хостами, и передавать в функцию два хоста, между которыми надо сделать соединение
        const hostConnection: BoxGeometry = new BoxGeometry(length, 0.5, 0.1);
        const hostConnectionMesh: Mesh = new Mesh(
            hostConnection,
            hostConnectionMaterial
        );
        hostConnectionMesh.position.set(0.5, 1, -0.45);

        // Создаем шар для анимации активного соединения
        const sphereGeometry: SphereGeometry = new SphereGeometry(0.1, 64, 32);
        const sphereMaterial: MeshBasicMaterial = new MeshBasicMaterial({
            color: 0xffff00,
            name: 'sphere',
        });
        const sphere: Mesh = new Mesh(sphereGeometry, sphereMaterial);
        sphere.position.set(0.2, 1.5, -0.1);

        const connectionGroup = new Group();
        connectionGroup.add(hostConnectionMesh);
        connectionGroup.add(sphere);

        this.#hostsConnections.push({
            id: id,
            connection: hostConnectionMesh,
            sphere: sphere,
        });

        this.#scene.add(connectionGroup);
    }

    createTextureLoader(): TextureLoader {
        // TODO: Вынести из класса, так же как и fontLoader?
        return new TextureLoader();
    }

    createCamera() {
        const aspect = window.innerWidth / window.innerHeight;
        const d = 10;

        const camera = new OrthographicCamera(
            -d * aspect,
            d * aspect,
            d,
            -d,
            1,
            1000
        );
        camera.position.set(20, 20, 20);
        camera.rotation.order = 'YXZ';
        camera.rotation.y = -Math.PI / 4;
        camera.rotation.x = Math.atan(-1 / Math.sqrt(2));
        return camera;
    }

    createWebGLRenderer(width: number, height: number): WebGLRenderer {
        const renderer: WebGLRenderer = new WebGLRenderer();
        renderer.shadowMap.enabled = true;
        renderer.shadowMap.type = PCFSoftShadowMap;
        renderer.setSize(width, height);
        document.body.appendChild(renderer.domElement);
        return renderer;
    }

    createLight(x: number, y: number, z: number) {
        const light = new DirectionalLight(0xffffff, 1.5);
        // const helper = new DirectionalLightHelper(light, 1, 0xff0000);
        light.position.set(10, 10, 0);

        // const helper = new PointLightHelper(light);
        // this.#gui.addColor(new ColorGUIHelper(light, 'color'), 'value').name('color');
        // this.#gui.add(light, 'intensity', 0, 2, 0.01);
        // this.#gui.add(light, 'distance', 0, 40).onChange(updateLight);
        // makeXYZGUI(this.#gui, light.position, 'position', updateLight);
        // makeXYZGUI(this.#gui, light.rotation, 'rotation', updateLight);
        // this.#scene.add(helper);
        // function updateLight() {
        //     helper.update();
        // }

        this.#scene.add(light);
    }

    createFloor(): void {
        const texture = this.#textureLoader.load(bg);
        texture.wrapS = RepeatWrapping;
        texture.wrapT = RepeatWrapping;
        texture.repeat.set(90, 90);
        texture.magFilter = NearestFilter;
        const floorMaterial: MeshStandardMaterial = new MeshStandardMaterial({
            name: 'floor',
            map: texture,
        });
        const floor: Mesh = new Mesh(
            new BoxGeometry(
                this.#floorSize.x,
                this.#floorSize.y,
                this.#floorSize.z
            ),
            floorMaterial
        );
        floor.position.set(0, -this.#floorSize.y / 2, 0);
        this.#scene.add(floor);
    }

    render(): void {
        if (this.#hostsConnections.length > 0) {
            this.#hostsConnections.forEach((connection) => {
                console.log(connection);
            });
        }
        this.#renderer.render(this.#scene, this.#camera);
        // @ts-ignore
        this.composer.render();
        requestAnimationFrame(() => this.render());
    }

    get scene(): Scene {
        return this.#scene;
    }

    get camera(): OrthographicCamera {
        return this.#camera;
    }

    get renderer(): WebGLRenderer {
        return this.#renderer;
    }

    get light(): PointLight {
        return this.#light;
    }
}

class ColorGUIHelper {
    object;
    prop;
    constructor(object: any, prop: any) {
        this.object = object;
        this.prop = prop;
    }
    get value() {
        return `#${this.object[this.prop].getHexString()}`;
    }
    set value(hexString) {
        this.object[this.prop].set(hexString);
    }
}

function makeXYZGUI(gui: any, vector3: any, name: any, onChangeFn: any) {
    const folder = gui.addFolder(name);
    folder.add(vector3, 'x', -10, 10).onChange(onChangeFn);
    folder.add(vector3, 'y', 0, 10).onChange(onChangeFn);
    folder.add(vector3, 'z', -10, 10).onChange(onChangeFn);
    folder.open();
}
